.header {
  padding: 20px 15px;
  padding-left: 0;
}

.header .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo {
  width: 50px;
  height: 50px;
}

.site-nav__list {
  display: flex;
  list-style: none;
  padding: 0;
  margin: 0;
}

.site-nav__link {
  display: block;
  min-width: 80px;
  text-align: center;
  font-family: "Lato", sans-serif;
  text-transform: uppercase;
  text-decoration: none;
  color: #868686;
  padding: 20px 10px;
}

.site-nav__link:hover {
  color: #ffffff;
  background-color: #ffbb42;
  border-radius: 5px;
}

.promo {
  position: relative;
  padding-top: 90px;
  padding-left: 40px;
  padding-right: 40px;
  padding-bottom: 345px;
  text-align: center;
  background-color: #709dca;
}

.promo__title {
  font-family: "Gotham", sans-serif;
  font-size: 35px;
  text-transform: uppercase;
  color: #ffffff;
  margin-bottom: 10px;
}

.promo__text {
  margin: 0;
  margin-bottom: 35px;
  font-size: 16px;
  font-weight: 300;
  color: #ffffff;
}

.promo__form {
  display: flex;
  justify-content: center;
}

.promo__slider {
  position: absolute;
  top: 0;
  left: 50%;
  width: 100%;
  transform: translate(-50%, 80%);
}

.form input {
  border: none;
  outline: none;
  border-radius: 2px;
  padding: 18px;
  padding-left: 40px;
  width: 281px;
  height: 46px;
  background-color: #ffffff;
  margin-right: 10px;
}

.form input::placeholder {
  font-size: 13px;
  color: #a9a9a9;
}

.form__name {
