*, *:before, *:after {
    box-sizing: border-box;
}

img {
    max-width: 100%;
    height: auto;
}

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;

  white-space: nowrap;

  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

body {    
    font-family: $font-stack;
    font-size: 13px;
    line-height: 22px;
    font-weight: 400;
    color: #7b7b7b;  
    
}

.container {
    max-width: $container-size;
    margin: 0 auto;
    padding: 0 $container-padding;
  }