.promo {
    position: relative;

    padding-top: 90px;
    padding-left: 40px;
    padding-right: 40px;
    padding-bottom: 345px;

    text-align: center;
    background-color: $promo-bgcolor;

    // .promo__title

    &__title {
        font-family: $font-title;
        font-size: 35px;
        text-transform: uppercase;
        color: #ffffff;

        margin-bottom: 10px;
    }

    // .promo__text

    &__text {
        margin: 0;
        margin-bottom: 35px;

        font-size: 16px;
        font-weight: 300;
        color: #ffffff;
    }

    &__form {
        display: flex;
        justify-content: center;
    }

    &__slider {

        position: absolute;
        top: 0;
        left: 50%;
        width: 100%;
        transform: translate(-50%, 80%);

    
    }



}



.form {
    input {
        border: none;
        outline: none;

        border-radius: 2px;
        padding: 18px;
        padding-left: 40px;
        width: 281px;
        height: 46px;
        background-color: #ffffff;
      
        margin-right: 10px;
     

        &::placeholder {
            font-size: 13px;
            color: #a9a9a9;
        }      

    }
    // .form__name

    &__name {
        position: relative;

        &::before {
            content: "";
            position: absolute;
            top: 15px;
            left: 15px;
            width: 15px;
            height: 15px;
            background-image: url('../img/icon-name.png');
            background-repeat: no-repeat;
        }
    }

    // .form__email

    &__email {
        position: relative;

        &::before {
            content: "";
            position: absolute;
            top: 15px;
            left: 15px;
            width: 15px;
            height: 15px;
            background-image: url('../img/icon-email.png');
            background-repeat: no-repeat;
        }
    }

    // .form__tel

    &__tel {
        position: relative;

        &::before {
            content: "";
            position: absolute;
            top: 15px;
            left: 15px;
            width: 15px;
            height: 15px;
            background-image: url('../img/icon-phone.png');
            background-repeat: no-repeat;
        }
    }

    &__submit {

        border: none;
        outline: none;
        border-radius: 2px;
        padding: 15px 40px;
        text-align: center;
        text-transform: uppercase;
        color: #ffffff;

        min-width: 211px;
        min-height: 46px;
        background-color: #ffbb42;

    }
}


.promo-slider-wrapper {
    position: absolute;
    top: 19px;
    left: 50%;
   transform: translateX(-50%);
}